import React, { useState } from 'react'
import { Swiper, SwiperSlide } from 'swiper/react'
import 'swiper/swiper-bundle.css'
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';
import './StyleSajda.css'

export default function ReusableSwiper({ data }) {
    const [photoIndex, setPhotoIndex] = useState(0);
    const [isOpen, setIsOpen] = useState(false);
    const [isMainOpen, setIsMainOpen] = useState(false);

    const openMainLightbox = () => {
        setIsMainOpen(true);
    };

    const openLightbox = (index) => {
        setPhotoIndex(index);
        setIsOpen(true);
    };

    return (
        <div className="style__slides">

            <div className="style__slide">
                <div className="styles__wrap">
                    <p className='styles__text'>{data.text}</p>

                </div>

                <div className="row">

                    <div className="col-lg-3 col-md-4 col-sm-5 col-6">
                        <div className="style__box">
                            <img
                                className='style__mainImg'
                                src={data.mainImg}
                                alt=""
                                onClick={openMainLightbox}
                            />
                            {isMainOpen && (
                                <Lightbox
                                    mainSrc={data.mainImg}
                                    onCloseRequest={() => setIsMainOpen(false)}
                                />
                            )}
                        </div>
                    </div>
                    <div className="col-lg-9 col-md-8 col-sm-7 col-6">

                        <div className="style__box-2">
                            <Swiper
                                autoplay={{ delay: 2500 }}
                                spaceBetween={20}
                                slidesPerView={3.5}
                                breakpoints={{
                                    0: {
                                        slidesPerView: 1.5,
                                    },
                                    576: {
                                        slidesPerView: 1.5,
                                    },
                                    768: {
                                        slidesPerView: 2.5,
                                    },
                                    992: {
                                        slidesPerView: 3.5
                                    },
                                    1400: {
                                        slidesPerView: 3.5,
                                    }
                                }}
                            >
                                {data.arrayOfImgs.map((el, index) => (
                                    <SwiperSlide key={index}>
                                        <div className="style__box">
                                            <img
                                                className='swiper__img'
                                                src={el}
                                                alt=""
                                                onClick={() => openLightbox(index)}
                                            />
                                        </div>
                                    </SwiperSlide>
                                ))}
                            </Swiper>

                            {isOpen && (
                                <Lightbox
                                    mainSrc={data.arrayOfImgs[photoIndex]}
                                    nextSrc={data.arrayOfImgs[(photoIndex + 1) % data.arrayOfImgs.length]}
                                    prevSrc={data.arrayOfImgs[(photoIndex + data.arrayOfImgs.length - 1) % data.arrayOfImgs.length]}
                                    onCloseRequest={() => setIsOpen(false)}
                                    onMovePrevRequest={() =>
                                        setPhotoIndex((photoIndex + data.arrayOfImgs.length - 1) % data.arrayOfImgs.length)
                                    }
                                    onMoveNextRequest={() =>
                                        setPhotoIndex((photoIndex + 1) % data.arrayOfImgs.length)
                                    }
                                />
                            )}
                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
}
