import React, { useState } from 'react'
import './Carousel.css'
import { Swiper, SwiperSlide } from 'swiper/react'
import 'swiper/swiper-bundle.css'
import Img1 from '../../assets/images/carousel/carousel1.jpg'
import Img2 from '../../assets/images/carousel/carousel2.jpg'
import Img3 from '../../assets/images/carousel/carousel3.jpg'
import Img4 from '../../assets/images/carousel/carousel4.jpg'
import Img5 from '../../assets/images/carousel/carousel5.jpg'
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';

export default function Carousel() {
    const images = [Img1, Img2, Img3, Img4, Img5];
    const [photoIndex, setPhotoIndex] = useState(0);
    const [isOpen, setIsOpen] = useState(false);

    const openLightbox = (index) => {
        setPhotoIndex(index);
        setIsOpen(true);
    };

    return (
        <section id='carousel'>
            <Swiper
            autoplay={{ delay: 2500 }} 
            spaceBetween={30} 
            slidesPerView={3.5}
            breakpoints={{
                0: {
                    slidesPerView:1.5,
                },
                576: {
                    slidesPerView:1.5,
                },
                768: {
                    slidesPerView:2.5,
                },
                996:{
                    slidesPerView:3.5
                },
                1400: {
                    slidesPerView:3.5,
                }
            }}
    
            >
                {images.map((img, index) => (
                    <SwiperSlide key={index}>
                        <img
                            className='swiper__img'
                            src={img}
                            alt=""
                            onClick={() => openLightbox(index)}
                        />
                    </SwiperSlide>
                ))}
            </Swiper>

            {isOpen && (
                <Lightbox
                    mainSrc={images[photoIndex]}
                    nextSrc={images[(photoIndex + 1) % images.length]}
                    prevSrc={images[(photoIndex + images.length - 1) % images.length]}
                    onCloseRequest={() => setIsOpen(false)}
                    onMovePrevRequest={() =>
                        setPhotoIndex((photoIndex + images.length - 1) % images.length)
                    }
                    onMoveNextRequest={() =>
                        setPhotoIndex((photoIndex + 1) % images.length)
                    }
                />
            )}
        </section>
    )
}
