import React from 'react';
import './Apply.css'
import { TextField, FormHelperText, Button, Snackbar } from '@mui/material';
import { MenuItem, Alert, IconButton } from '@mui/material';
import { flags, codes } from './constants';
import emailjs from 'emailjs-com';
import CloseIcon from '@mui/icons-material/Close';

const maxPhoneLengths = {
    '+996 (KG)': 9,  // Kyrgyzstan
    '+7 (KZ)': 10, // Kazakhstan
    '+7 (RU)': 10, // Russia
    '+998 (UZ)': 9,  // Uzbekistan
    '+971 (UAE)': 9,  // UAE
};


const Apply = () => {
    const [formValues, setFormValues] = React.useState({
        country: 'KG',
        code: '+996 (KG)',
        tel: '',
        name: '',
        email: '',
    });

    const [nameError, setNameError] = React.useState({ error: false, errorMessage: "" });
    const [telError, setTelError] = React.useState({ error: false, errorMessage: "" });
    const [alertMessage, setAlertMessage] = React.useState({ open: false, message: '', type: 'success' });
    const [emailError, setEmailError] = React.useState({ error: false, errorMessage: "" });

    const [loader, setLoader] = React.useState(false)

    console.log(formValues)
    const handleFormChange = (event) => {
        const { name, value } = event.target;

        if (name === 'name') {
            let error = false;
            let errorMessage = '';

            if (value.length < 2 || value.length > 50) {
                error = true;
                errorMessage = 'ФИО должно содержать от 2 до 50 символов и не может начинаться или заканчиваться пробелом.';
            }
            setNameError({ error, errorMessage });
        }

        if (name === 'tel') {
            let error = false;
            let errorMessage = '';

            if (value.length > maxPhoneLengths[formValues.code] || (value.length < maxPhoneLengths[formValues.code])) {
                error = true;
                errorMessage = `Номер телефона должен состоять из ${maxPhoneLengths[formValues.code]} цифр.`;
            }
            setTelError({ error, errorMessage });
        }

        if (name === 'email') {
            let error = false;
            let errorMessage = '';

            // Basic email pattern
            const pattern = /^[^ ]+@[^ ]+\.[a-z]{2,3}$/;

            if (!value.match(pattern)) {
                error = true;
                errorMessage = 'Введите действительный адрес электронной почты.';
            }
            setEmailError({ error, errorMessage });
        }

        setFormValues(prevState => ({
            ...prevState,
            [name]: value,
        }));
    };

    const handleCloseAlert = () => {
        setAlertMessage((prevMessage) => ({
            ...prevMessage,
            open: false,
        }));
    };


    const sendEmail = (e) => {
        e.preventDefault();
        if (!nameError.error && !telError.error && formValues.name && formValues.tel && formValues.country && formValues.email) {
            setLoader(true)
            emailjs.sendForm('service_uj0kydt', 'template_2mkfr7e', e.target, 'fQOzxiHoStYL4xXEb')
                .then((result) => {
                    console.log(result.text);
                    setAlertMessage({ open: true, message: 'Email успешно отправлен!', type: 'success' });
                    setLoader(false)
                }, (error) => {
                    console.log(error.status);
                    setAlertMessage({ open: true, message: 'Произошла ошибка при отправке email. Попробуйте снова.', type: 'error' });
                    setLoader(false)
                });
        }
    };

    return (
        <section id="apply">
            <Snackbar
                open={alertMessage.open}
                onClose={handleCloseAlert}
            >
                <Alert
                    style={{ backgroundColor: 'gold' }}
                    severity={alertMessage.type}
                    action={
                        <IconButton
                            size="small"
                            aria-label="close"
                            color="inherit"
                            onClick={handleCloseAlert}
                        >
                            <CloseIcon fontSize="small" />
                        </IconButton>
                    }
                >
                    {alertMessage.message}
                </Alert>
            </Snackbar>
            <div className="container">
                <div className="row gy-5">
                    <h2>Остались вопросы?</h2>

                    <div className="col-lg-6 col-md-12 col-sm-12 col-12">
                        <p className='apply__text1'>Заполните заявку!</p>
                        <form
                            className="apply__box"
                            onSubmit={sendEmail}
                        >
                            {nameError.error && <FormHelperText error={true}>{nameError.errorMessage}</FormHelperText>}

                            <TextField
                                name='name'
                                value={formValues.name}
                                style={{ width: '100%' }}
                                id="outlined-basic"
                                label="ФИО"
                                variant="outlined"
                                onChange={handleFormChange}
                                error={nameError.error}

                            />
                            {telError.error && <FormHelperText error={true}>{telError.errorMessage}</FormHelperText>}
                            <div className='apply__box--inner'>
                                <TextField
                                    name='code'
                                    value={formValues.code}
                                    style={{ width: '30%' }}
                                    id="flagged-input"
                                    select
                                    label="Код"
                                    onChange={formValues.tel ? (e) => setFormValues(prev => ({ ...prev, code: e.target.value, tel: '' })) : handleFormChange}
                                >
                                    {codes.map((option, index) => (
                                        <MenuItem key={index} value={option.code}>
                                            {/* <img className='apply__img' src={option.imgSrc} alt="" /> */}
                                            <span className='apply__hide'>{option.code}</span>
                                        </MenuItem>
                                    ))}
                                </TextField>
                                <TextField
                                    name='tel'
                                    type='number'
                                    value={formValues.tel}
                                    style={{ width: '70%' }}
                                    error={telError.error}
                                    id="outlined-basic"
                                    label="Телефон"
                                    variant="outlined"
                                    onChange={handleFormChange}
                                />

                            </div>

                            <TextField
                                name='country'
                                value={formValues.country}
                                style={{ width: '100%' }}
                                id="flagged-input"
                                select
                                label="Выбери страну"
                                onChange={handleFormChange}
                            >
                                {flags.map((option) => (
                                    <MenuItem key={option.value} value={option.value}>
                                        <img className='apply__img' src={option.imgSrc} alt={option.label} />
                                        <span className='apply__hide' style={{ display: 'inline-block', marginLeft: '10px' }}>{option.label}</span>
                                    </MenuItem>
                                ))}
                            </TextField>
                            {emailError.error && <FormHelperText error={true}>{emailError.errorMessage}</FormHelperText>}

                            <TextField
                                name='email'
                                value={formValues.email}
                                style={{ width: '100%' }}
                                id="outlined-basic"
                                label="Email"
                                variant="outlined"
                                onChange={handleFormChange}
                                error={nameError.error}

                            />
                            <Button
                                variant="outlined"
                                type='submit'
                                disabled={nameError.error || telError.error || emailError.error || !formValues.name || !formValues.tel || !formValues.country || !formValues.email || loader}
                            >
                                Отправить заявку
                            </Button>

                        </form>
                    </div>

                    <div className="col-lg-6 col-md-12 col-sm-12 col-12  apply__contactsBox">
                        <div className='apply__text2'>
                            <p>Контакты</p>
                        </div>
                        <div className="apply__contacts">
                            <div className="apply__contactsPhone">
                                <p className='apply__p'>Тел.:</p>
                                <a className='apply__a' href="tel:+996550309505">+996(550)309-505</a>
                            </div>
                            <div className="apply__contactsEmail">
                                <p className='apply__p'>Почта:</p>
                                <a className='apply__a' href="mailto:officialsajda@gmail.com">officialsajda@gmail.com</a>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </section>

    );
};

export default Apply;