import React from 'react';
import './Franchise.css';
import franchiseImg from '../../assets/images/franchiseLogo.png';
import 'bootstrap/dist/css/bootstrap.min.css';

const Franchise = () => {
    return (
        <section id='franchise'>
            <div className="container">
                <div className="row gy-5 franchise__wrap">

                    <div className="col-lg-8 col-md-12 col-sm-12 col-12">
                        <div className="franchise__info">
                            <h2>О Франшизе SAJDA</h2>
                            <p className='franchise__info-p'>Это возможность построить свой успешный бизнес, присоединившись <br /> к сообществу в более 10 странах и удерживающих высокую конкурентоспособность, качества на протяжении 7 лет, к дружной семье предпринимателей и сильной команде специалистов, готовых оказать любую поддержку ради общей цели.</p>
                            <p className='franchise__info-p'>В настоящее время наши коллекции уже продаются  в Казахстане,Узбекистане, ОАЭ, России,США, Южной Корее,Африке, Австралии,Норвегии,Турции  через дистрибуцию.</p>
                            <p className='franchise__info-p'>И это нас сподвигло запустить франчайзинговую сеть, так как количество желающих открыть бизнес с нашей франшизой растет с каждым годом. А наша одежда становится популярнее с каждым днем, удовлетворяя верующих в религии Ислам. </p>
                            {/* <button className='franchise__btn'>Получить финмодель</button> */}
                            <a className='franchise__btn' href='#apply'>Отправить заявку</a>
                        </div>
                    </div>

                    <div className="col-lg-4 col-md-12 col-sm-12 col-12">
                        <div className="franchise__box">
                            <img src={franchiseImg} alt="" />
                        </div>
                    </div>

                </div>
            </div>
        </section>
    );
};

export default Franchise;